.project {
  padding: 1rem;
  width: 4rem;
  background-color: var(--ternary-color);
  /* height: 35vh; */
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  color: var(--primary-color--darker);
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-about {
  /* position: relative; */
  /* height: 23vh; */
  display: flex;
  flex-direction: column;
  align-content: space-between;
}

.project-about p {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0.5rem;
  padding-right: 0.7rem;
}

.name {
  margin: 0 0 1rem 0;
  padding-right: 1rem;
  /* color: var(--secondary-color) */
  /* display: flex;
    justify-content: right; */
}

.name::after {
  content: "";
  display: block;
  height: 0.1rem;
  width: 10rem;
  background-color: var(--primary-color--lighter);
  margin: 0.5rem auto;
  opacity: 0.6;
}

.secondary-heading {
  margin: 0;
  padding: 0.6rem 0.5rem;
  /* text-transform: uppercase; */
  text-align: left;
  background-color: var(--quaternary-color--lighter);
  transform: translate(-1rem);
  width: 13rem;
  /* border-radius: 0 1rem 1rem 0; */
}

.project-button {
  /* height: 2.5rem;
  width: 13rem; */
  align-self: flex-end;
  min-width: 11rem;
  font-weight: bold;
  font-size: 1.1rem;
  /* text-transform: uppercase; */
  /* font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
}

.svg {
  height: 1rem;
  margin-left: 1rem;
  /* filter: invert(45%); */
}

@media (min-width: 500px) {
  .project {
    padding: 3rem;
    max-width: 90vw;
    margin: 0 auto;
  }
  .secondary-heading {
    transform: translate(-3rem);
  }
}

@media (min-width: 750px) {
  .project {
    max-width: 80vw;
    min-height: 17.5rem;
  }
}

@media (min-width: 1000px) {
  .project {
    padding: 1rem;
    text-align: left;
    /* height: 35vh; */
  }
  .name {
    text-align: center;
    margin-bottom: 0;
  }
  .secondary-heading {
    transform: translate(-1rem);
  }
  .bottom {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 1400px) {
  .used-technologies {
    padding-bottom: 3rem;
  }
}
